import React from 'react';
import Header from '../../components/Header';
import { Row, Col, Jumbotron } from 'reactstrap';
import './style.css';


const About = (props) => {
    return (
        <div id="o-mnie" className="content" style={{ paddingTop: "10px" }}>
            <hr />
            <Row>
                <Col cs="12" md="4">
                    <img id="profilePhoto" src={require("../../assets/images/about_me.jpg")} />
                </Col>
                <Col xs="12" md="8">
                    <div style={{ textAlign: "left" }}>
                        {/* <h2>Cze!</h2> */}
                        <p>Mam na imi Ania. Fotografia jest moj wielk pasj  w tych kilku sowach mieci si moja, ju prawie dziesicioletnia, przygoda z aparatem. </p>
                        <p>Dzisiaj fotografia towarzyszy mi na co dzie, a kada sesja zdjciowa z wami sprawia mi rado i daje ch do dalszego rozwoju. </p>
                        <p>Od niedawna mam szczcie nazywania swojej pasji rwnie prac. W mojej ofercie jest fotografia, zarwno plenerowa, jak i studyjna:</p>
                        <ul>
                            <li>fashion</li>
                            <li>lifestyle</li>
                            <li>portretowa</li>
                            <li>narzeczeska, realizuj te filmy  podzikowania dla rodzicw</li>
                            <li>rodzinna</li>
                        </ul>
                        <p>Obszar mojego dziaania to nie tylko lsk, w ktrym mieszkam, ale take caa Polska.</p>
                        <p>Chtnie uwieczni Was na zdjciach! </p>
                        <p>Zapraszam do wsppracy!</p>

                    </div>
                </Col>
            </Row>
        </div>
    );

}

export default About;